  font-size: 54px;
  line-height: 65px;
  margin: 0 auto;
}
.price__period-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
  z-index: 2;
}
.price__period-item {
  position: relative;
}
.price__period-item::after {
  position: absolute;
  content: "";
  width: 100%;
  bottom: 0;
  height: 2px;
  background-color: rgba(16, 38, 84, 0.5);
}
.price__period-link {
  width: 100%;
  height: 100%;
  color: rgba(16, 38, 84, 0.5);
}
.price__period-link-title {
  font-size: 20px;
}
.price__card-list {
  display: grid;
  grid-template-columns: repeat(3, minmax(320px, 442px));
  column-gap: 3%;
  row-gap: 8px;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
  z-index: 2;
}
@media (max-width: 1199px) {
  .price__card-list {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
}
@media (max-width: 1199px) {
  .price__card-item {
    margin: 0 auto;
    width: 57%;
    min-width: 290px;
    max-width: 442px;
  }
}

.card {
  background: linear-gradient(148.82deg, #D10A42 0.85%, #00359F 100%);
  padding: 7px;
}
.card__wrapper {
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  padding: 68px 23px;
}
.card__title {
  text-align: center;
  margin: 0;
  margin-bottom: 22px;
  font-weight: 700;
  font-size: 26px;
  line-height: 29px;
  color: #102654;
}
@media (max-width: 767px) {
  .card__title {
    margin-bottom: 20px;
  }
}
.card__subtitle {
  color: #495A7D;
  text-align: center;
  margin: 0;
  margin-bottom: 80px;
}
@media (max-width: 767px) {
  .card__subtitle {
    margin-bottom: 70px;
  }
}
.card__price-container {
  position: relative;
  display: flex;
  margin: 0 auto;
  margin-bottom: 59px;
}
.card__price-container::after {
  font-size: 100px;
  font-weight: 700;
  color: #E8E8E8;
  opacity: 0.48;
  position: absolute;
  top: 7px;
  right: 50%;
  transform: translateX(50%);
  content: "1700";
  z-index: 1;
}
.card__price {
  font-weight: 700;
  font-size: 60px;
  line-height: 150%;
  color: #103AAE;
  z-index: 2;
}
.card__button {
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  display: block;
  padding: 24px;
  box-sizing: border-box;
  width: 206px;
  margin: 0 auto;
  background-color: #ffffff;
  border: 2px solid #102654;
  text-align: center;
  color: #102654;
}

.game__wrapper {
  display: flex;
  box-sizing: content-box;
  margin: 0 auto;
  padding: 120px 7.5%;
  padding-bottom: 53px;
  max-width: 1160px;
}
@media (max-width: 1199px) {
  .game__wrapper {
    padding: 100px 5.8%;
    padding-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .game__wrapper {
    padding: 70px 4.6%;
    padding-bottom: 35px;
  }
}

.game-offer {
  display: flex;
  width: 100%;
}
@media (max-width: 767px) {
  .game-offer {
    display: flex;
    flex-direction: column;
  }
}
.game-offer__content {
  display: grid;
  grid-template-columns: 58% 26.5%;
  grid-template-rows: 1fr 1fr auto 1fr;
  column-gap: 15.5%;
  row-gap: 33px;
  padding: 85px 78px;
  align-items: top;
  background-color: #ED0233;
  width: 43%;
  color: #ffffff;
}
@media (max-width: 767px) {
  .game-offer__content {
    width: 100%;
  }
}
.game-offer__image-container {
  display: block;
  width: 57%;
}
.game-offer__image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 767px) {
  .game-offer__image-container {
    width: 100%;
  }
}
.game-offer__title {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 82px;
  line-height: 96%;
  display: block;
  margin: 0;
  align-self: center;
  grid-row-start: 1;
  grid-row-end: 3;
}
.game-offer__subtitle {
  font-size: 18px;
  line-height: 150%;
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
}
.game-offer__information-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  grid-row-start: 1;
  grid-row-end: 3;
}
.game-offer__term {
  margin: 0;
  margin-bottom: 17px;
  font-size: 18px;
}
.game-offer__term ~ .game-offer__term {
  margin-top: 32px;
}
.game-offer__long-date {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
.game-offer__definition {
  display: flex;
  align-items: center;
  margin: 0;
  font-weight: 700;
  font-size: 40px;
}
.game-offer__button {
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  display: block;
  padding: 29px;
  color: #ffffff;
  grid-column: span 2;
  border: 4px solid #FFFFFF;
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto;
  background-color: transparent;
}

.instructors {
  max-width: 100%;
  height: auto;
}
.instructors__wrapper {
  box-sizing: content-box;
  display: flex;
  margin: 0 auto;
  padding: 120px 7.5%;
  padding-bottom: 53px;
  max-width: 1160px;
}
@media (max-width: 1199px) {
  .instructors__wrapper {
    padding: 100px 5.8%;
    padding-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .instructors__wrapper {
    padding: 70px 4.6%;
    padding-bottom: 35px;
  }
}
.instructors__list {
  display: flex;
}

.instructors__slide {
  min-height: 294px;
}
@media (max-width: 767px) {
  .instructors__slide {
    aspect-ratio: 82/100;
  }
}
.instructors__slide:last-child {
  background-color: red;
}

.swiper {
  width: 100%;
}

.instructors__slide:hover .instructor__title {
  position: static;
}
.instructors__slide:hover .instructor__image-container {
  z-index: -1;
}

.instructor {
  color: #ffffff;
}
.instructor__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: rgba(16, 38, 84, 0.89);
  padding: 58px 20px;
}
.instructor__title {
  margin: 0 auto;
  margin-bottom: 5px;
  position: absolute;
  bottom: 5px;
  z-index: 5;
}
.instructor__subtitle {
  margin: 0 auto;
  margin-bottom: 30px;
}
.instructor__advantages {
  padding: 0;
  margin: 0;
}
.instructor__advantage {
  margin: 0;
  margin-bottom: 24px;
}
.instructor__image-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.instructor__image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

